//FastClick.attach(document.body);

router.beforeEach((to, from, next) => {
	//document.title = i18n.t('title.title');
	document.title = to.meta.title;
	next();
});

router.afterEach((to,from,next) => {
	let url = window.location.href;
	var cut_url = url.indexOf("#");
	var res_url = url.substring(0, cut_url);
	var querys = res_url.substring(url.indexOf('?') + 1).split('&');
	var result=[];
	var link = '';
	for(var i=0;i<querys.length;i++){
		var temp=querys[i].split('=');
		if(temp.length<2){
			result[temp[0]]='';
		}else{
			result[temp[0]]=temp[1];
		}
	}
	if(to.query.link != undefined && to.query.link != '' && to.query.link != null){
	  	link = to.query.link;
	}else{
		link = result.link;
	}
	window.location.href = link;
});

new Vue({
  router,
  render: h => h(App),
}).$mount('#app')
