import Vue from 'vue'
import Router from 'vue-router'
Vue.use(Router);

export default new Router({
	//mode: 'history',
	scrollBehavior(to, from, savePosition) {
		if(savePosition){
			return savePosition
		}else{
			return { x: 0, y: 0 }
		}
	},
	routes: [
		{path: '*', redirect: '/index'},
		//英语
		{path: '/index', name: 'Index', component: resolve => require(['@/pages/index/index'], resolve), meta: {title: '', requireAuth: true}},
	]
})